import { Button, Select, Form, Modal, Input, message } from "antd";
import React from "react";

class CreateDoc extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      nodeName: "",
      nodeType: "DOC",
    };
  }

  createDoc = () => {
    this.setState({
      visible: false,
    });
    this.props.createDoc(this.state.nodeName, this.state.nodeType);
  };

  hideModel = () => {
    this.setState({
      visible: false,
    });
  };

  showModal = () => {
    if (!this.props.hasChooseWorkspace()) {
      message.error("");
    } else {
      this.setState({
        nodeName: "",
        visible: true,
      });
    }
  };

  onNameChange = (e) => {
    const { value } = e.target;
    this.setState({ nodeName: value });
  };

  onTypeChange = (value) => {
    console.log(value);
    this.setState({ nodeType: value });
  };

  render() {
    const { Option } = Select;
    return (
      <div>
        <Button ghost style={{ border: "none" }} onClick={this.showModal}>
          
        </Button>
        <Modal
          title=""
          visible={this.state.visible}
          onOk={this.createDoc}
          onCancel={this.hideModel}
          okText=""
          cancelText=""
        >
          <Form name="basic">
            <Form.Item
              label=""
              name="docName"
              defaultValue=""
              rules={[{ required: true, message: "Please input docName" }]}
            >
              <Input
                onChange={(e) => {
                  const { value } = e.target;
                  this.setState({ nodeName: value });
                }}
              />
            </Form.Item>
            <Form.Item label="" name="docType">
              <Select defaultValue="DOC" onChange={this.onTypeChange}>
                <Option value="DOC"></Option>
                <Option value="WORKBOOK"></Option>
                <Option value="MIND"></Option>
                <Option value="FOLDER"></Option>
              </Select>
            </Form.Item>
          </Form>
        </Modal>
      </div>
    );
  }
}

export default CreateDoc;
