import React from "react";
import { Modal, Button, Table, message, Select } from "antd";
import axios from "axios";

class AddMember extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      userInfoList: [],
    };
  }

  hideModel = () => {
    this.setState({
      visible: false,
    });
  };

  showList = () => {
    axios
      .get(this.props.domain + "/biz/users", {})
      .then((res) => {
        if (res.data.success) {
          let userList = res.data.data;
          console.log(userList);
          this.setState({
            visible: true,
            userInfoList: userList,
          });
        } else {
          message.error(res.data.errorMsg);
        }
      })
      .catch((err) => {
        alert("getUserInfo err, " + JSON.stringify(err));
      });
  };

  render() {
    const { Option } = Select;
    const columns = [
      {
        title: "",
        dataIndex: "name",
        key: "name",
      },
      {
        title: "",
        render: (text) => (
          <Select
            style={{ width: 120 }}
            onChange={(value) => {
              this.props.choosePermission(value);
            }}
          >
            <Option value="ONLY_VIEWER">ONLY_VIEWER</Option>
            <Option value="VIEWER">VIEWER</Option>
            <Option value="EDITOR">EDITOR</Option>
          </Select>
        ),
      },
      {
        title: "",
        render: (text) => (
          <Button
            type="primary"
            onClick={() => {
              this.hideModel();
              this.props.addUserPermission(text);
            }}
          >
            
          </Button>
        ),
      },
    ];
    return (
      <div>
        <Button ghost style={{ border: "none" }} onClick={this.showList}>
          
        </Button>
        <Modal
          title=""
          visible={this.state.visible}
          onOk={this.hideModel}
          okText=""
          onCancel={this.hideModel}
          cancelText=""
        >
          <Table
            rowKey={(record) => record.unionid}
            columns={columns}
            dataSource={this.state.userInfoList}
          ></Table>
        </Modal>
      </div>
    );
  }
}

export default AddMember;
